import {Options} from "../options/options";
import {handleOperations, fetchOperations} from "./operations";

export function toHandleSubmit(options: Partial<Options>) {
    return (event: Event) => {
        const form = event.currentTarget as HTMLFormElement;

        if (form.tagName.toUpperCase() !== 'FORM') return;
        event.preventDefault();

        const formData: any = new FormData(form);
        let body = form.method.toUpperCase() === 'GET'
            ? [...formData.entries()].map(pair => `${encodeURIComponent(pair[0])}=${pair[1]}`).join('&') //Array.from(formData.entries(), e => e.map(encodeURIComponent).join('=')).join('&')
            : formData;

        const fetchOptions = {
            ...options,
            method: form.method,
            url: form.action,
            body: body,
        };

        fetchOperations(fetchOptions).then(handleOperations);
    }
}



