import {Options} from "./options/options";
import {AjaxOptions} from "options/ajaxOptions";
import {toHandleClick} from "./handler/click";
import {toHandleSubmit} from "./handler/submit";
import {popStateHandler, resetPopStateEvent} from "./handler/history";
import {setOptions} from "./options";

export function ajaxcom(options?: Partial<Options & AjaxOptions>) {
    const links = document.querySelector(options.linksSelector || "a:not([target=_blank])");
    const forms = document.querySelector(options.formsSelector || "form");

    if (links) links.addEventListener('click', toHandleClick(options));
    if (forms) forms.addEventListener('submit', toHandleSubmit(options));

    resetPopStateEvent();
    window.onpopstate = popStateHandler;

    setOptions(options);
}



